/* start carousel*/
#hero-carousel {
    max-height: 600px;
    overflow-y: hidden;
    .overlay {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.5);
    }
}
